<template>
    <div class="lineup_panel">
        <div class="container">
            <div class="box" v-for="team,index in $store.battle.lineupList" :key="team.index" ref="box" 
            @touchstart="$refs.box[index].classList.add('click')"
            @touchend="$refs.box[index].classList.remove('click')"
            @click="Check(team)">
                <div class="title_bg"></div>
                <div class="title">{{ team.lineupName == '' ? "未命名" : team.lineupName }}</div>
                <div class="warrior_list">
                    <div class="warrior_box" v-for="w in team.list" :key="w.index">
                        <div class="warrior_card">
                            <WarriorElongatedCard :warriorData="w" cardStyle="2_1" :showUpArms="true" />
                        </div>
                    </div>
                </div>
                <div class="number">{{ index + 1 }}</div>
                <div class="create_time">创建时间：{{ team.createTime }}</div>
            </div>
            <div class="empty_tip" v-if="$store.battle.lineupList.length == 0">还没有阵容保存</div>
        </div>
        <div class="detail_panel" v-if="display">
            <div class="mask" @click="display = false"></div>
            
            <div class="container">
                <div class="close_btn" ref="close_btn" 
                @touchstart="$refs.close_btn.classList.add('click')"
                @touchend="$refs.close_btn.classList.remove('click')" 
                @click="display = false"></div>
                <div class="topic_texture"></div>
                <div class="topic_text">
                    {{ curTeam.lineupName ? curTeam.lineupName : "未命名" }}
                </div>
                <div class="warrior_list">
                    <div class="warrior_box" v-for="w in curTeam.list" :key="w.index">
                        <div class="warrior_card">
                            <WarriorCard :warriorData="w" :showUpArms="true" cardStyle="2_3" />
                        </div>
                        <div class="other_list">
                            <div class="tactics_box" v-for="t,tIdx in w.tacticsArr" :key="t" v-show="t.id != -1 && tIdx">
                                <div class="icon">
                                    <TacticsIcon :tacticsData="t" />
                                </div>
                            </div>
                            <div class="block"></div>
                            <div class="arms_spec" v-for="spec,spId in w.armsCommonSpec[w.choseUpArmsIdx]" :key="spec.index" 
                            v-show="spId == w.choseArmsCharacterIdx[0] || spId == w.choseArmsCharacterIdx[1]">
                                <img :src="ArmSpecIconImg(spec.type)" alt="" class="spec_img">
                                <div class="text">{{ spec.name }}</div>
                            </div>
                            <div class="block"></div>
                            
                            <div class="artifact_box" v-if="w.artifact">
                                <!-- <img :src="AritifactImgIcon(w.artifact)" alt="" class="art_up_sign">
                                <div class="name">{{ w.artifact.name }}</div>
                                <div class="forge_eff">
                                    <img :src="ForgeEffImgIcon(w.artifact)" class="cover">
                                    <div class="text cover">{{ w.artifact.forgeEffArr[w.artifact.forgeChoseIdx].name }}</div>
                                </div> -->
                                <ArtifactCard :artifactData="w.artifact" />
                            </div>
                            <div class="plus_box" v-for="plusValue,attrKey in  w.plus" :key="attrKey.index" v-show="plusValue > 0">
                                {{ `${attrMap[attrKey]}加点+${plusValue}` }}
                            </div>
                        </div>


                    </div>
                    
                </div>
                <div class="apply_btn_left" ref="apply_btn_left"
                @touchstart="$refs.apply_btn_left.classList.add('click')"
                @touchend="$refs.apply_btn_left.classList.remove('click')"
                @click="ApplyLineup($store.battle.teams.list[0],0)">
                应用到我方</div>
                <div class="apply_btn_right" ref="apply_btn_right" 
                @touchstart="$refs.apply_btn_right.classList.add('click')"
                @touchend="$refs.apply_btn_right.classList.remove('click')"
                @click="ApplyLineup($store.battle.teams.list[1],1)">
                应用到敌方</div>
                <div class="delete_btn" ref="delete_btn" 
                @touchstart="$refs.delete_btn.classList.add('click')"
                @touchend="$refs.delete_btn.classList.remove('click')"
                @click="DeleteThis()">删 除</div>
            </div>
        </div>
        <!-- <div class="random_chose_left" ref="random_chose_left" 
        @touchstart="$refs.random_chose_left.classList.add('click')"
        @touchend="$refs.random_chose_left.classList.remove('click')"
        @click="RandomApplyLineup($store.battle.BlueTeam)"
        >随机应用我方</div>
        <div class="random_chose_right" ref="random_chose_right"
        @touchstart="$refs.random_chose_right.classList.add('click')"
        @touchend="$refs.random_chose_right.classList.remove('click')"
        @click="RandomApplyLineup($store.battle.RedTeam)"
        >随机应用敌方</div> -->
        <div class="clear_all_btn" ref="clear_all_btn" @click="ClearAll()"
        @touchstart="$refs.clear_all_btn.classList.add('click')"
        @touchend="$refs.clear_all_btn.classList.remove('click')"
        v-if="$store.battle.lineupList.length > 0"
        >清空阵容</div>
        <TopNav title="阵 容" />
    </div>
</template>

<script>
import WarriorElongatedCard from '@/components/style/WarriorElongatedCard.vue';
import TopNav from '@/components/container/TopNav.vue';
import WarriorCard from '@/components/style/WarriorCard.vue';
import TacticsIcon from '@/components/style/TacticsIcon.vue';
import ArtifactCard from '@/components/style/ArtifactCard.vue';
import { BattleUtil } from '@/util/battle';
import Warrior from '@/util/warrior';
export default {
    name : 'Lineup',
    components:{
        TopNav,
        WarriorElongatedCard,
        WarriorCard,
        TacticsIcon,
        ArtifactCard
    },
    data(){
        return{
            display : false,
            curTeam : null,
            attrMap:{
                ad : "攻击",
                de : "防御",
                speed : "速度",
                tact : "谋略",
            }
        }
    },
    methods:{
        AritifactImgIcon(art){
            switch(art.effQualityIdx){
                case 0 :
                case 1 : return require('../../assets/ui/ui (65).png');
                case 2 : return require('../../assets/ui/ui (73).png');
            }
        },
        ForgeEffImgIcon(art){
            switch(art.effQualityIdx){
                case 0 : return require('../../assets/ui/ui (182).png');
                case 1 : return require('../../assets/ui/ui (184).png');
                case 2 : return require('../../assets/ui/ui (183).png');
            }
        },
        Check(data){
            this.curTeam = data;
            this.display = true;
        },
        //高级兵种特性图标
        ArmSpecIconImg(type){
            switch(type){
                case "步" : return require('../../assets/ui/ui (124).png')
                case "骑" : return require('../../assets/ui/ui (122).png')
                case "弓" : return require('../../assets/ui/ui (123).png')
            }
        },
        //应用某个阵容
        ApplyLineup(whichTeam,teamIdx){
            for(let i in whichTeam){
                let warrior = new Warrior();
                warrior.CopyData(this.curTeam.list[i]);
                whichTeam[i] = warrior;
            }
            this.$store.Alert('应用成功');
            this.$router.back();
            // this.$router.push({
            //     path : '/battle',
            //     query : {
            //         teamIdx : teamIdx
            //     }
            // })
            // this.display = false;
        },
        //删除某个阵容
        DeleteThis(){
            if(this.$store.battle.lineupList.length == 0) return
            let that = this;
            let f = function(){
                let u = new BattleUtil(that.$store);
                let res = u.DeleteLineup(that.curTeam.list)
                if(res){
                    that.display = false;
                    that.$store.Alert('删除成功!');
                }
                else that.$store.Alert('删除失败!');
            }
            let params = {
                msg : `确定从阵容中删除这个队伍吗？`,
                CallBack : f,
            }
            this.$store.Prompt(params)
        },
        //清除所有阵容
        ClearAll(){
            let that = this;
            let f = function(){
                let u = new BattleUtil(that.$store);
                u.ClearAllLineup();
                // that.$bus.$emit('alert','清除成功');
                that.$store.Alert('清除成功');
            }
            let params = {
                msg : `确定清空这${that.$store.battle.lineupList.length}支队伍吗`,
                CallBack : f,
            }
            this.$store.Prompt(params)
            // let params = {
            //     text : `确定清除所有${that.$store.battle.LineupContainer.length}个阵容？`,
            //     callBack : f,
            // }
            // this.$store.commit('Alert',params);
        }
    },
    mounted(){

    }

}
</script>

