                            width: 14vw; height: 14vw;
                            margin: 0 0 1vw 3vw;
                            // background: #000;
                            .icon{
                                position: relative; float: left;
                                left: 1vw; top: 1vw;
                                width: 12vw; height: 12vw;
                            }
                        }
                        .arms_spec{
                            position: relative; float: left;
                            width: 20vw; height: 5vw;
