                            box-shadow: 0 10px 50px black;
                            .name{
                                left: 4.5vw; top: 0;
                                line-height: 6vw;
                                font-family: 'FZLBJW';
                            }
                            .art_up_sign{
                                left: .5vw; top: 1.3vw;
