<script>
import ReportItem from '@/components/style/ReportItem.vue';
import Report from '@/battle/js/report';
import TopNav from '@/components/container/TopNav.vue';
import WarriorCard from '@/components/style/WarriorCard.vue';
import ArtifactElCard from '@/components/style/ArtifactElCard.vue';
// import Statistics from './Statistics.vue';
export default {
    name :'BattleReport',
    components : {
        // Statistics
        WarriorCard,
        ReportItem,
        TopNav,
        ArtifactElCard
    },
    data(){
        return{
            teams : this.$store.battle.battleResult,
            game : this.$store.battle,
            show : {
                statistics : false,
                navigation : false,
            },
            NAV_HEAP : [],
            viewHeight : window.innerHeight/100,//对应1vw的px
            viewWidth : window.innerWidth/100,//对应1vw的px

            reportShowRule:{
                "兵种特性":false,
                "宝物特性":false,
                "战斗":true,
                "开局信息":false,
            }
            
        }
    },
    beforeCreate(){

    },
    methods:{
        ShowRule(report){
            return this.reportShowRule[report.fromType];
        },  
        //获取队伍的原始总兵力
        TotalHp(team){
            let res = 0;
            for(let i in team){
                res += team[i].hpMax;
            }
            return res;
        },
        //获得对应的战报层数
        GetReportHeight(){
            this.NAV_HEAP = [];
            for(let i=0;i<this.game.ReportHeap.length;i++){
                if(this.game.ReportHeap[i].type == '系统'){
                    this.NAV_HEAP.push(i);
                }
            }
            this.NAV_HEAP.push(0);
        },
        //导航电梯
        NavLift(position){
            if(position == this.NAV_HEAP.length - 1){
                this.$refs.result_warrior_container.scrollTo(0,1000000);
                this.CloseNavigateList();
            }
            else if(position == 0){
                this.$refs.result_warrior_container.scrollTo(0,0);
                this.CloseNavigateList();
            }
            else{
                let reports = document.querySelectorAll('.report');
                let distance = reports[this.NAV_HEAP[position-1]].offsetTop;
                
                // let high = this.$store.state.equipment == 'phone' ? 150 : 32;
                let high = 150;
                distance += high * this.viewWidth;
                console.log(reports,this.game.ReportHeap,distance,this.NAV_HEAP,position)
                this.$refs.result_warrior_container.scrollTo(0,distance);
                this.CloseNavigateList();
            }
        },
        //回合导航
        NavigateList(){
            this.show.navigation = true;
            this.$refs.result_warrior_container.classList.add('filter');
            this.$refs.report_show_control.classList.add('filter');
        },
        CloseNavigateList(){
            this.show.navigation = false;
            this.$refs.result_warrior_container.classList.remove('filter');
            this.$refs.report_show_control.classList.remove('filter');
        },
        //汉字
        TransferChineseWords(number){
            switch(number){
                case 1 : return "一";
                case 2 : return "二";
                case 3 : return "三";
                case 4 : return "四";
                case 5 : return "五";
                case 6 : return "六";
                case 7 : return "七";
                case 8 : return "八";
            }
        },
        LoadingTeamsData(){
            
        },
        //初始化血量
        LoadingBloodTape(){
            let blueTotal = this.TotalHp(this.teams[0]);
            let redTotal = this.TotalHp(this.teams[1]);
            let blue_w = Math.floor(((this.teams[0][0].hp + this.teams[0][1].hp + this.teams[0][2].hp) / blueTotal)*100);
            let red_w = Math.floor(((this.teams[1][0].hp + this.teams[1][1].hp + this.teams[1][2].hp) / redTotal)*100);
            let blue_wound_w = 0;
            for(let i in this.teams[0]){
                blue_wound_w += this.teams[0][i].hp + this.teams[0][i].wound;
            }
            blue_wound_w = Math.floor((blue_wound_w/blueTotal)*100);
            let red_wound_w = 0;
            for(let i in this.teams[1]){
                red_wound_w += this.teams[1][i].hp + this.teams[1][i].wound;
            }
            red_wound_w = Math.floor((red_wound_w/blueTotal)*100);
            if(blue_w < 0){
                blue_w = 0
            }else if(blue_w > 100){
                blue_w = 100;
            }
            if(red_w < 0){
                red_w = 0;
            }else if(red_w > 100){
                red_w = 100;
            }
            // let size = this.$store.state.equipment == 'phone' ? 50 : 25;
            let size = 50;
            this.$refs.blood_blue[0].style.width = (blue_w/100) * size + 'vw';
            this.$refs.wound_blue[0].style.width = (blue_wound_w/100) * size + 'vw';
            this.$refs.blood_red[0].style.width = (red_w/100) * size + 'vw';
            this.$refs.wound_red[0].style.width = (red_wound_w/100) * size + 'vw';
        },
        //打开统计面板
        OpenStatisticsPanel(){
            this.$router.push({
                path : `${this.$route.path}/report_statistics`,
            })
        }   
    },
    mounted(){
        setTimeout(() => {
            this.LoadingBloodTape();
        }, 100);
        //加载战报
        // let report = new Report();
        // report.BuildReport(this.$refs.report_text,this.$store.battle.ReportHeap);
        this.GetReportHeight();
    }
}
</script>

