import Save from "./save";
import Warrior from "./warrior";

export class Lineup {
    constructor() {
        this.lineupName = "";
        this.describe = "";
        this.createTime = "";
        //存放武将信息
        this.list = [];
    }
    Init(teamData) {
        this.lineupName = `【${teamData[0].name}】队`;
        this.GetCreateTime();
        this.BuildList(teamData)
    }
    GetCreateTime() {
        let currentTime = new Date(); // 创建一个表示当前时间的Date对象
        let year = currentTime.getFullYear(); // 获取年份
        let month = (currentTime.getMonth() + 1).toString().padStart(2, '0'); // 获取月份（注意要加上1）
        let day = currentTime.getDate().toString().padStart(2, '0'); // 获取天数
        let hours = currentTime.getHours().toString().padStart(2, '0'); // 获取小时
        let minutes = currentTime.getMinutes().toString().padStart(2, '0'); // 获取分钟
        let seconds = currentTime.getSeconds().toString().padStart(2, '0'); // 获取秒数

        // 组合为完整的日期和时间字符串
        this.createTime = `${year}/${month}/${day} ${hours}:${minutes}:${seconds}`;
    }
    BuildList(teamData) {
        for (let i in teamData) {
            let item = {
                red: teamData[i].red,
                level: teamData[i].level,
                id: teamData[i].id,
                name: teamData[i].name,
                tacticsConfig: [],
                artifactConfig: null,
                plus: teamData[i].plus,
                isUp: teamData[i].isUp,
                isUpArms: teamData[i].isUpArms,
                choseUpArmsIdx: teamData[i].choseUpArmsIdx,
                choseArmsCharacterIdx: teamData[i].choseArmsCharacterIdx,
            }
            //保存战法配置
            for (let j = 0; j < teamData[i].tacticsArr.length; j++) {
                if (j > 0) {
                    let tactItem = {
                        id: teamData[i].tacticsArr[j].id,
                        name: teamData[i].tacticsArr[j].name,
                        type: teamData[i].tacticsArr[j].type,
                    }
                    item.tacticsConfig.push(tactItem);
                }
            }
            //保存宝物信息
            if (teamData[i].artifact != null) {
                let artItem = {
                    name: teamData[i].artifact.name,
                    effQualityIdx: teamData[i].artifact.effQualityIdx,
                    forgeChoseIdx: teamData[i].artifact.forgeChoseIdx
                }
                item.artifactConfig = artItem;
            }
            this.list.push(item);
        }
    }
}

export class BattleSetting {
    constructor(){
        defWarriorLevel = 50;//武将默认等级
        defMoral = [100,100];//默认士气
    }
}



export class BattleUtil {
    constructor(store) {
        this.store = store
    }
    //保存阵容数据
    AddLineup(teamData) {
        let lineup = new Lineup();
        lineup.Init(teamData)
        this.store.battleStorage.battleLineups.push(lineup);
        let emptyTeam = {
            lineupName: lineup.lineupName,
            describe: "",
            createTime: lineup.createTime,
            //存放武将信息
            list: [],
        }
        for (let i in teamData) {
            let warrior = new Warrior();
            warrior.CopyData(teamData[i]);
            emptyTeam.list.push(warrior);
        }
        this.store.battle.lineupList.push(emptyTeam);
        let s = new Save(this.store);
        s.SaveBattleData();
        return true;
    }
    //删除阵容
    DeleteLineup(delTeam) {
        for (let i in this.store.battle.lineupList) {
            if (this.store.battle.lineupList[i].list == delTeam) {
                this.store.battle.lineupList.splice(i, 1);
                this.store.battleStorage.battleLineups.splice(i, 1);
                let s = new Save(this.store);
                s.SaveBattleData();
                return true;
            }
        }
        return false;
    }
    //清空所有阵容
    ClearAllLineup() {
        this.store.battle.lineupList = [];
        this.store.battleStorage.battleLineups = [];
        let s = new Save(this.store);
        s.SaveBattleData();
    }
}



